import ComInputCom from '@/components/ComInputCom.vue';


const cutdownTime = 60;
export default {
	data() {
		return {
			phone: '', //手机号
			code: '', //验证码
			iscode: '', //用于存放验证码接口里获取到的code
			codename: ' 获取验证码 ',
			disabled: false,
			// phoneClearBtnShow: false,
			
			cutdownIng: false, // 正在倒计时中，
			cutDownTiemr: '', // 倒计时定时器
			sendBtnText: '获取验证码',
			isFoucs:false
		};
	},
	computed: {
		allowLogin () {
			return !!(this.phone && this.code)
		},
		phoneClearBtnShow(){
			return !!this.phone
		}
	},
	components: {
		ComInputCom
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord();
	},
	onUnload() {
		if (this.cutDownTiemr) {
			clearInterval(this.cutDownTiemr);
		}
	},
	methods: {
		onBackToOtherLogin(){
			uni.reLaunch({
				url:'/pages/login/login'
			})
		},
		getPhoneValue: function(e) {
			if (e.detail.value.length == 11) {
				this.disabled = false;
			}
			this.phone = e.detail.value;
		},
		getCodeValue: function(e) {
			this.code = e.detail.value;
		},
		sendVeCode() {
			let _this = this;
			if (this.cutdownIng) return;
			//判断手机号格式
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (this.phone == '') {
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			uni.request({
				method: 'POST',
				data: {
					mobile: this.phone
				},
				//短信接口
				url: _this.url + 'users/forgot/sms/',

				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success(res) {
					//根据code判断
					console.log(res)
					var ocode = res.statusCode;
					if (ocode == 200) {
						_this.iscode = res.data.data;
						let surTime = cutdownTime;
						_this.cutdownIng = true;
						_this.sendBtnText = `${surTime}s后重新获取`;
						_this.cutDownTiemr = setInterval(() => {
							surTime--;
							_this.sendBtnText = `${surTime}s后重新获取`;
							if (surTime <= 0) {
								clearInterval(_this.cutDownTiemr);
								_this.cutdownIng = false;
								_this.sendBtnText = '获取验证码';
								_this.cutDownTiemr = '';
							}
						}, 1000);
					} else if (ocode == 400) {
						uni.showToast({
							title: '用户不存在',
							icon: 'none',
							duration: 2000
						});
						return false;
					} else if (ocode == 411) {
						uni.showToast({
							title: '操作太频繁，请稍候重试',
							icon: 'none',
							duration: 2000
						});
						return false;
					}
				},
				fail: () => {
				}
			});
		},
