		// },
		//提交表单信息
		next: function() {
			var _this = this;
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (this.phone == '') {
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			if (this.code == '') {
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			if (this.code != this.iscode) {
				uni.showToast({
					title: '验证码错误',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: _this.url + 'users/forgot/next/',
				method: 'POST',
				data: {
					mobile: this.phone,
					code: this.code
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success(res) {
					if (res.statusCode == 401) {
						uni.showToast({
							title: 'wrong',
							icon: 'none',
							duration: 2000
						});
					}
					if (res.statusCode == 400) {
						uni.showToast({
							title: '手机号验证码不匹配',
							icon: 'none',
							duration: 2000
						});
					}
					if (res.statusCode == 200) {
						uni.navigateTo({
							url: '../setNewPassword/setNewPassword?code=' + _this.code + '&phone=' + _this.phone
						});
					}
				}
			});
		},
